import React from 'react';
import './App.css';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  
} from "react-router-dom";

import Landing from './Screen/Landing';

import Development from './Screen/Development';
import Design from './Screen/Design';
import Support from './Screen/Support';

import Portfolio from './Screen/Portfolio';
import Blog from './Screen/Blog';
import Services from './Screen/Services';
import Team from './Screen/Team';
function App() {

  
  
  return (
      <Router>
      <div>
       

        <Switch>
          <Route exact from='/' to='/Landing' component={ Landing } /> 
          <Route  from='/Development' to='/Development' component={ Development } /> 
          <Route  from='/Design' to='/Design' component={ Design } /> 
          <Route  from='/Support' to='/Support' component={ Support } /> 

          <Route from='/Services' to='/Services' component={ Services } />
          <Route from='/Portfolio' to='/Portfolio' component={ Portfolio } />
          <Route from='/Blog' to='/Blog' component={ Blog } /> 
          <Route from='/Team' to='/Team' component={ Team } /> 
         
        </Switch>
      </div>
    </Router>
  );
}

export default App;
